(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('ViewUserController', ViewUserController);
	
	/** @ngInject */
	function ViewUserController(UserService) {
		var vm = this;
		
		function loadProfile(){
			UserService.getUserByID(1)
				.then(function(profile){
					vm.profile = profile;
				});
		}
		
		loadProfile();
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.service('UserService', UserService);
	
	/** @ngInject */
	function UserService($resource, backend_url) {
		var service = {
			getAllUsers: getAllUsers,
			getUserByID: getUserByID,
			createUser: createUser,
			updateUser: updateUser,
			deleteUser: deleteUser,
			getAllGenders: getAllGenders
		};
		
		var UserResource =  $resource(backend_url+'/users/:id', {}, {
			query: { method: 'GET', isArray: true },
			create: { method: 'POST' },
			show: { method: 'GET' },
			update: { method: 'PUT', params: {id: '@id'} },
			delete: { method: 'DELETE', params: {id: '@id'} }
		});
		
		return service;
		
		function getAllUsers(){
			return UserResource.query().$promise;
		}
		
		function getUserByID(ID){
			return UserResource.show({id:ID}).$promise;
		}
		
		function createUser(User){
			return UserResource.create(User).$promise;
		}
		
		function updateUser(User){
			return UserResource.update(User).$promise;
		}
		
		function deleteUser(UserID){
			return UserResource.delete({id:UserID}).$promise;
		}
		
		function getAllGenders(){
			return [{id: 'M', name: 'Hombre'},{id: 'F', name: 'Mujer'}];
		}
		
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('RegisterUserController', RegisterUserController);
	
	/** @ngInject */
	function RegisterUserController(UserService, toastr) {
		var vm = this;
		
		vm.profile = {gender: "M", admin: false};
		vm.genders = UserService.getAllGenders();
		vm.registerUser = registerUser;
		
		function registerUser(){
			UserService.createUser(vm.profile)
				.then(function(){
					toastr.success('El usuario fue registrado con exito', 'Oferta creada');
				})
				.catch(function(){
					toastr.error('Hubo un error al intentar registrar el usuario!', 'Error!');
				});
		}
	}
})();

