(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('OfferController', OfferController);

	/** @ngInject */
	function OfferController(OfferService, $mdDialog) {
		var vm = this;
		
		vm.openCreateDialog = openCreateDialog;
		
		function loadOffers(){
			OfferService.getAllOffers()
				.then(function(offers){
					vm.offers = offers;
				});
		}
		
		function openCreateDialog($event){			
			$mdDialog.show({
				controller: 'CreateOfferController',
				controllerAs: 'vm',
				templateUrl: 'app/offer/create/create_offer.html',
				targetEvent: $event,
				clickOutsideToClose:true
			});
		}
		
		loadOffers();
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('MainController', MainController);

	/** @ngInject */
	function MainController($mdSidenav,app_menu, $location) {
		var vm = this;

		vm.toggleMenu = toggleMenu;
		vm.menus = app_menu;
		vm.changeLocation = changeLocation;

		function toggleMenu() {
			$mdSidenav('left').toggle();
		}

		function changeLocation(url){
			$location.url(url);
		}
	}
})();
(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.service('LoginService', LoginService);

	function LoginService($resource, backend_url) {
		var service = {
			makeLogin: makeLogin
		};
		
		var LoginResource =  $resource(backend_url+'/users/login', {}, {
			login: { method: 'POST' }
		});
		
		return service;
		
		function makeLogin(userData){
			return LoginResource.login(userData).$promise;
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('ingeSoftIi')
		.controller('LoginController', LoginController);

	function LoginController(LoginService, $location, toastr) {
		var vm = this;
		
		vm.userData = {};
		vm.login = login;
		vm.redirect = redirect;
		
		function login(){
			LoginService.makeLogin(vm.userData)
				.then(function(response){
					console.log("Correcto");
					redirect('/offer');
				})
				.catch(function(error){
					console.log("error");
					toastr.error('Hubo un error al intentar acceder a su cuenta!', 'Error!');
				});
		}
		
		function redirect(location){
			$location.url(location);
		}
	}
})();

